@import url(https://fonts.googleapis.com/css?family=Open+Sans|Roboto:400,700&display=swap);
/* Navigation Bar  */

.navbar {
    background: #fff;
    box-shadow: 0 3px 6px 0 rgb(50 50 50 / 30%);
}

.company-name {
    font-family: 'Noto Serif', serif;
    font-size: 1.5rem;
    margin: 0 1rem 0 0.5rem;
    color: var(--clr-primary-2)
}

i {
    font-size: 1.5rem;
    margin: 0 0.5rem;
    color:black;
}
/* Description  */

.description {
    margin-top: 3rem;
}

.description-title {
    margin: auto;
    /* width:100%; */
    text-align: center;
    font-weight: 600;
    /* text-align:center; */
    font-size: 3rem;
    font-family: rawline, Open Sans, Arial, sans-serif
}

.description-content {
    margin: 0 auto;
    width: 80%;
    font-size: 1.5rem;
}


@media (max-width:600px) {
    .description-title {
        /* width:80%; */
        font-size: 2rem;
        margin-left: 0.45rem;
        text-align: left
    }

    .description-content {
        margin-left: 0.45rem;
        font-size: 1rem;
    }
}
/* Input Link Section  */

.input {
    margin: 3rem auto;
    width: 50vw
}

/* Buttons */

.link-btn {
    background: var(--clr-primary-10);
    color: var(--clr-primary-5);
    padding: 0.25rem 0.5rem;
    text-transform: capitalize;
    border-radius: var(--radius);
    transition: var(--transition);
    border-color: var(--clr-primary-5);
    cursor: pointer;
    margin: 0.5rem 0.5rem;
}

.link-btn:hover {
    background: var(--clr-primary-5);
    color: var(--clr-primary-1)
}

.order-btn {
    margin-top: 2rem;
    justify-content: center;
}

@media (max-width:600px) {
    .input {
        margin: 2rem 0 0 0.45rem;
        width: 90vw;
    }
}

.login-container{
    display:flex;
    justify-content:center;
    width: 30%;
    box-shadow: 0 6px 16px 0 rgb(0 0 0/20%);    
}

.password-input span{
    font-size: 1rem;
    height: 100%; 
    margin-left: -0.5em; 
    background: white; 
    border-left: none;
}

.password-input-box{
    border-right:none
}

.signup-btn{
    background: var(--clr-primary-5);
    color: var(--clr-primary-1);
    width:70%
}

.line{
    border: 0;
    /* width: 33%; */
    height: 0;
    border-bottom: 2px solid #cdcdcd;
}

.signup , .social-login{
    display:flex;
    align-items:center;
}

.social-login-icons{
    display:flex;
    justify-content:space-evenly;
    margin:auto;
    text-align:center;
}

.social-login-icons img{
    width:40x;
    height:40px;
}

@media (max-width:900px) {
    .login-container{
        width:50%
    }
}
@media (max-width:500px) {
    .login-container{
        width:85%
    }
}
.form-label{
    margin-bottom:0;
}
